<style lang="stylus">

  * {
    margin: 0;
    padding: 0;
  }
  html, body {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
  }

  body {
    background-color: #fbf9fe;
  }

  #app {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content:center;
    align-items: center;
    height: 100%;
    width: 100%;
  }
</style>

<template>
  <div id="app" ref="app">
    <img :src="erweima.imgLink" style="width: 100%"/>
  </div>
</template>

<script>
  export default {
    name:'App',
    data() {
      return {
        erweima: ''
      }
    },
    mounted () {

      let _this = this

      //window.location.href
      let _wlh = window.location.href
      console.log('window.location.href =>', window.location.href)
      //sharedUserid=\[[^\]]+\]  获取第一个
      if (_wlh) {
        let reg = new RegExp('suid=[0-9]+')
        let rs = reg.exec(window.location.href)
        if (rs) {
          let str_ = rs[0]
          let arr_ = str_.split('=')
          if (arr_ && arr_.length > 1) {
            let suid = arr_[1]
            console.log('suid =>', suid)


            _this.$http.post(`http://47.98.182.61:8081/qRCode/getImgLink?imgdomainId=${suid}`)
              .then(response=>{
                console.log('得到图片地址！', response.data);
                _this.erweima = response.data
              }).catch(function (error) {
              console.log('请求二维码图片地址失败！',error);
            })


          }
        }
      }

    }
  }
</script>


